import React from "react";

const Song = ({ currentsong, isPlaying }) => {
  return (
    <div className="song-container">
      <img
        className={isPlaying ? "rotate" : ""}
        src={currentsong.cover}
        alt=""
      ></img>
      <h1>{currentsong.name}</h1>
      <h3>{currentsong.artist}</h3>
    </div>
  );
};

export default Song;
