import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faMusic } from "@fortawesome/free-solid-svg-icons";
const Nav = ({ closeLibraryHandler }) => {
  return (
    <nav>
      <h1>IBenChi</h1>
      <button className="library-toggle" onClick={closeLibraryHandler}>
        <FontAwesomeIcon icon={faMusic} />
        <span></span> Library
      </button>
    </nav>
  );
};

export default Nav;
