import LibrarySong from "./LibrarySong";

const Library = ({
  songs,
  currentsong,
  setcurrentsong,
  audioref,
  isPlaying,
  libraryOpen,
  closeLibraryHandler,
  setsongs,
}) => {
  return (
    <div
      className={`library ${libraryOpen ? "libraryOpen" : "libraryNotOpen"}`}
    >
      <h2>
        Library{" "}
        <span onClick={closeLibraryHandler} className="close-cross">
          x
        </span>
      </h2>

      <div className="library-songs">
        {songs.map((song) => (
          <LibrarySong
            key={song.id}
            song={song}
            audioref={audioref}
            isPlaying={isPlaying}
            songs={songs}
            currentsong={currentsong}
            setsongs={setsongs}
            setcurrentsong={setcurrentsong}
          />
        ))}
      </div>
    </div>
  );
};

export default Library;
